@import url(https://fonts.googleapis.com/css2?family=Asap:wght@600&family=DM+Serif+Display&family=Questrial&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'DM Serif Display', serif;
}

.nav-container {
  display: flex;
  width: 100vw;
  padding: 1em 3em;
  align-items: center;
  background-color: #F4F4F4;
}

#basket-icon:hover {
cursor: pointer;
}

#logo-link{
  text-decoration: none;
  color: black;
}

.logo {
  font-size: 2em;
}

.nav-link {
  text-decoration: none;
  color: black;
  font-size: 1.2em;
}
.nav-container .nav-bar .nav-list {
  display: flex;
  list-style: none;
  margin: 0 1em;
}

.logo-container {
  flex: 2 1;
}

.basket-container {
  flex: 1 1;
}

.nav-bar {
  flex: 6 1;
}

.nav-bar .nav-list .nav-item {
  padding: 0 2em;
}

ul li {
  text-decoration: none;
}
#logo-span {
  color: #FE1708;
}
.row{ 
  float: left;
  width: 25%;
  padding: 20px;
}

.row::after {
  content: "";
  clear: both;
  display: table;
}
.images{
  float: none;
  margin-top: 12px;
  width: 100%;
  height: 200px;
  border-radius: 5px;
  max-width: 350px;
  
} 
.row:hover{
  border-radius: 20px;
  background-color: #F1F1F1;
  box-shadow: 3px 3px rgb(180, 175, 175);
  
}
.text{
  font-size: large;
  width: 100%;
  height: 100%;
  font-family: 'DM Serif Display', sans-serif;
  margin: 5px;
}
.price{
  font-size: large;
  font-weight: bolder;
  width: 100%;
  height: 100%;
  margin-left: 70%;

}
button{
  height: 40px;
  width: 120px;
  border-radius: 15px;
  background-color: #FE1708;
  color: white;
  font-size: 13px;
  font-weight: 600;
  /* margin-left: 60%; */
  font-family: 'Asap', sans-serif;
  font-family: 'Questrial', sans-serif;
  border: none;
  
}
button:hover{
  background-color: #df0f00;
  cursor: pointer;

}
.card{
  background-color: #F1F1F1;
  padding: 10px;
  border-radius: 15px;
}

* {
  margin: 0;
  padding: 0;
}

#largestText,
#subText {
  text-align: start;
  font-size: 72px;
  margin-top: 10%;
  margin-left: 10%;
}

#largestText {
  font-weight: 400;
  font-size: 100px;
}

#subText {
  font-size: 32px;
  font-weight: 50;
  margin-top: 0;
}

#samplePara {
  margin-left: 10%;
}

#catsButton {
  margin-top: 5%;
  margin-left: 10%;
  border-radius: 10px;
  font-weight: bolder;
  border: none;
}

#buttonParent {
  -webkit-filter: drop-shadow(2px 4px 4px rgba(0, 0, 0, 0.25));
          filter: drop-shadow(2px 4px 4px rgba(0, 0, 0, 0.25));
}

p {
  position: absolute;
  z-index: 100;
  width: 697px;
  height: 80px;
  left: 0px;
  top: 559px;

  font-family: DM Serif Text;
  font-style: normal;
  font-weight: normal;
  font-size: 17px;
  line-height: 36px;

  color: #000000;
}

#catParent {
  z-index: 0;
}

#catImage {
  z-index: 1;
  position: absolute;
  width: 517px;
  height: 405px;
  left: 700px;
  top: 214px;
}

